@import '../base.scss';

.c-faq {
  &.c-faq-homepage {
    .c-faq__accordion {
      margin-bottom: 0;
    }
    .c-faq__inner {
      @include t-v-gutters(true);
    }
  }
  &__text {
    color: $s-color--blue-green;
  }

  &__header {
    background-color: $s-color--background-tertiary;

    &-inner {
      @include t-gutters(true);
      padding-top: t-to-rem(130);
      padding-bottom: t-to-rem(40);
    }

    &-heading {
      color: $s-color--font-6;
      text-align: center;
      margin-bottom: t-to-rem(50);
    }
  }

  &__category {
    cursor: pointer;
  }

  &__inner {
    padding-top: t-to-rem(152);
    padding-bottom: 8%;
    @include t-gutters;
  }

  &__categories {

    &-heading {
      text-align: center;
      margin-bottom: t-to-rem(14);
    }
  }

  &__accordion {
    display: none;
    transition: all $s-anim--time-short;
    opacity: 1;
    margin-bottom: t-to-rem(58);

    &.is-visible {
      display: block;
    }

    &.is-fade-in {
      transform: translateY(-20px);
      opacity: 0;
    }

    &.is-fade-out {
      transform: translateY(20px);
      opacity: 0;
    }

    &-slide {
      overflow: hidden;
      position: relative;
      border-bottom: 1px solid rgba($s-color--black, .2);

      &:last-child {
        border-bottom: unset;
      }

      &-title {
        list-style: none;
        align-items: center;
        display: flex;
        cursor: pointer;
        user-select: none;
        list-style: none;
        outline: none;
        padding: 1.5rem 0;
        font-weight: $s-font--weight-normal;

        &-inner {
          display: inline-block;
          padding-right: 1.75rem;
        }

        &-icon {
          width: t-to-rem(14);
          transform: rotate(0deg);
          transition: all .2s;
          position: absolute;
          right: 0;

          &.is-minus {
            opacity: 0;
          }

          &.is-plus {
            opacity: 1;
          }

          .is-expanded & {
            transform: rotate(180deg);

            &.is-minus {
              opacity: 1;
            }

            &.is-plus {
              opacity: 0;
            }
          }
        }

        // Hide summary marker
        &::marker,
        &::-webkit-details-marker {
          display: none;
        }
      }

      &-body {
        padding-bottom: 0;
        max-height: 0;
        transition: all .3s;

        .is-expanded & {
          padding-bottom: t-to-rem(22);
          max-height: t-to-rem(400);
        }

        p {
          margin: unset;

          a {
            text-decoration: underline;
          }
        }

        a {
          text-decoration: underline;
        }

        ul,
        ol {
          display: block;
          margin-top: 1em;
          margin-bottom: 1 em;
          margin-left: 0;
          margin-right: 0;
          padding-left: 40px;
        }

        ol {
          list-style-type: decimal;
        }

        ul {
          list-style-type: disc;
        }

        li {
          color: $s-color--font-8;
        }
      }
    }
  }

  @include t-media-query($s-small-down) {

    &__contact {
      &-image-container {
        max-width: t-to-rem(450);
        margin: 0 auto;

        &.is-first {
          margin-bottom: t-to-rem(32);
        }

        &.is-second {
          margin-top: t-to-rem(52);
        }
      }

      &-heading {
        margin-bottom: t-to-rem(24);
      }

      &-text {
        margin-bottom: t-to-rem(24);
      }
    }

    &__header {
      &-inner {
        max-width: 400px;
        margin: 0 auto;
      }
    }

    &__pre-title {
      text-align: center;
      margin-bottom: t-to-rem(35);
    }

    &__text {
      margin-bottom: t-to-rem(72);
      text-align: center;
    }

    &__image-container {
      margin-bottom: t-to-rem(-150);
    }

    &__categories {
      &-container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: t-to-rem(10);
        margin-bottom: t-to-rem(55);
      }
    }

    &__category {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      height: t-to-rem(140);
      background-color: $s-color--background-quaternary;
      transition: all .3s;

      &.is-active {
        background-color: $s-color--background-tertiary;
      }

      &-icon {
        display: block;
        @include t-width-and-height(56);
        border-radius: 100%;
        background: $s-color--white;
        padding: t-to-rem(14);
        margin-bottom: t-to-rem(10);
      }

      &-name {
        .is-active & {
          color: $s-color--font-6;
        }
      }
    }
  }

  @include t-media-query($s-small-up) {
    &__header {
      &-inner {
        padding-top: 13%;
        padding-bottom: 8%;
        display: flex;
      }

      &-left-container {
        margin-right: 10%;
      }

      &-heading {
        text-align: left;
      }
    }

    &__pre-title {
      margin-bottom: t-to-rem(40);
    }

    &__text {
      max-width: 48ch;
    }

    &__image-container {
      width: 34.6%;
      margin-bottom: -20%;
    }

    &__categories {
      margin-bottom: t-to-rem(70);

      &-container {
        display: flex;
        justify-content: space-between;
      }

      &-heading {
        margin-bottom: t-to-rem(32);
      }
    }

    &__category {
      display: flex;
      align-items: center;

      &-icon {
        @include t-width-and-height(56);
        border: 1px solid $s-color--background-tertiary;
        border-radius: 100%;
        display: block;
        padding: t-to-rem(13);
        transition: all $s-anim--time-short;
        margin-right: t-to-rem(10);

        .is-active & {
          background-color: $s-color--background-tertiary;
        }
      }
    }

    &__accordion-slide {

      &-title {
        height: t-to-rem(100);

        &-icon {
          width: t-to-rem(16);
        }
      }

      &-body {
        .is-expanded & {
          padding-bottom: t-to-rem(28);
        }
      }
    }
    &:not(.c-faq-homepage) {
      .c-faq__main-container {
        margin-bottom: 10.46%;
      }
    }


    &__contact {
      display: flex;

      &-image-container {
        width: 26%;
        flex-shrink: 0;

        &.is-first {
          margin-right: 13%;
        }

        &.is-second {
          margin-top: 11%;
          margin-left: 13%;
        }
      }

      &-heading {
        margin-bottom: t-to-rem(32);
      }

      &-text {
        margin-bottom: t-to-rem(60);
      }

      &-content {
        align-self: center;
      }
    }
  }

  @include t-media-query($s-medium-up) {
    &__main-container {
      margin-left: auto;
      margin-right: auto;
      width: 70%;
    }

    &__accordion-slide {

      &-title {
        height: t-to-rem(126);

        &-icon {
          width: t-to-rem(18);
        }
      }

      &-body {
        .is-expanded & {
          padding-bottom: t-to-rem(32);
        }
      }
    }

    &__categories {
      margin-bottom: t-to-rem(80);

      &-heading {
        margin-bottom: t-to-rem(36);
      }
    }

    &__category-icon {
      margin-right: t-to-rem(20);
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__main-container {
      width: 54.65%;
    }

    &__pre-title {
      margin-bottom: t-to-rem(58);
    }

    &__categories {
      margin-bottom: t-to-rem(90);
      padding: 0 4.67%;

      &-heading {
        margin-bottom: t-to-rem(44);
      }
    }

    &__category-icon {
      margin-right: t-to-rem(40);
    }
  }
}